.cart-dropdown {
  position: absolute;
  width: 240px;
  height: 340px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  border: 1px solid black;
  background-color: white;
  top: 90px;
  right: 40px;
  z-index: 5;
}
.cart-dropdown .cart-items {
  height: 240px;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  scrollbar-color: black;
  scrollbar-width: thin;
}
.cart-dropdown button {
  margin-top: auto;
}/*# sourceMappingURL=cart-dropdown.styles.css.map */